import { Component, Input, HostListener, EventEmitter, OnInit } from '@angular/core';
import { fadeInAnimation } from '../../../../shared/utils/animations';

@Component({
  selector: 'app-diagram-popup',
  templateUrl: './diagram-popup.component.html',
  styleUrls: ['./diagram-popup.component.less'],
  animations: [ fadeInAnimation ]
})
export class DiagramPopupComponent implements OnInit  {

  @Input() popupAction: EventEmitter<any> = new EventEmitter<any>();

  public state = false;
  public data;

  constructor() { }

  ngOnInit() {

    this.popupAction
      .subscribe(value => {
        this.state = value.state;
        this.data = value.data;
      });

  }

  public closeJsonTab() {
    this.state = false;
  }

  public onActivityClick(event) {
    console.log(event);
  }

  @HostListener('window:keyup', ['$event'])
  keyEvent(event: KeyboardEvent) {
      if (event.keyCode === 27) { // 27 - Esc keycode
      this.state = false;
      }
  }

}
