  private ACTIVITY_FILL_COLOR = '#f9f9f9';
  private MAIN_STROKE_COLOR = '#585858';

  private TEXT_PADDING = 3;
  private ARROW_WIDTH = 4;
  private MARKER_WIDTH = 12;

  private TASK_FONT = {font: '11px Arial', opacity: 1, fill: Raphael.rgb(0, 0, 0)};

  // icons
  private ICON_SIZE = 16;
  private ICON_PADDING = 4;

  private INITIAL_CANVAS_WIDTH;
  private INITIAL_CANVAS_HEIGHT;

  private paper;
  private viewBox;
  private viewBoxWidth;
  private viewBoxHeight;

  private canvasWidth;
  private canvasHeight;

  private nodeIndex = 0;

  private infoData: any = null;

  private scrollFlag = false;

