    const path1 = this.paper.path('M 6.75,16 L 25.75,16 M 16,6.75 L 16,25.75');
    path1.attr({
      'stroke-width': 3,
      'stroke': strokeColor,
      'fill': 'none'
    });

    path1.transform('T' + (element.x + 4) + ',' + (element.y + 4));

