  private _drawEventIcon(paper, element) {
    if (element.eventDefinition && element.eventDefinition.type) {
      if ('timer' === element.eventDefinition.type) {
        this._drawTimerIcon(paper, element);
      } else if ('error' === element.eventDefinition.type) {
        this._drawErrorIcon(paper, element);
      } else if ('signal' === element.eventDefinition.type) {
        this._drawSignalIcon(paper, element);
      } else if ('message' === element.eventDefinition.type) {
        this._drawMessageIcon(paper, element);
      }
    }
  }

  private _drawTimerIcon(paper, element) {
    const x = element.x + (element.width / 2);
    const y = element.y + (element.height / 2);

    const strokeColor = this._bpmnGetColor(element, this.MAIN_STROKE_COLOR);

    // let circle = paper.circle(x, y, 10);

    // circle.attr({'stroke-width': 1,
    //   'stroke': strokeColor,
    //   'fill': 'none'
    // });

    const path = paper.path(`M12,20A8,8 0 0,0 20,12A8,8 0 0,0 12,4A8,8 0 0,0 4,12A8,
    8 0 0,0 12,20M12,2A10,10 0 0,1 22,12A10,10 0 0,1 12,22C6.47,22 2,17.5 2,12A10,10 0 0,1 12,2M12.5,7V12.25L17,14.92L16.25,16.15L11,13V7H12.5Z`);
    path.attr({
      'stroke': strokeColor,
      'stroke-width': 1,
      'fill': 'none'
    });
    path.transform('T' + (element.x + 3) + ',' + (element.y + 3));
    return path;
  }

  private _drawErrorIcon(paper, element) {
    const path = paper.path('m 6,21.66 0.07791666666666668,-0.021083333333333336 6.1160000000000005,-8.008916666666668 6.389166666666667,7.47175 3.916916666666667,-15.183666666666667 -4.89225,9.708416666666666 -6.083,-7.9878333333333345 z');

    const strokeColor = this._bpmnGetColor(element, this.MAIN_STROKE_COLOR);

    let fill = 'none';
    let x = element.x;
    let y = element.y;
    if (element.type === 'EndEvent') {
      fill = strokeColor;
      x -= 1;
      y -= 1;
    }

    path.attr({
      'stroke': strokeColor,
      'stroke-width': 1,
      'fill': fill
    });

    path.transform('T' + x + ',' + y);
    return path;
  }

  private _drawSignalIcon(paper, element) {
    const strokeColor = this._bpmnGetColor(element, this.MAIN_STROKE_COLOR);

    let fill = 'none';
    if (element.type === 'ThrowEvent') {
      fill = strokeColor;
    }

    const path = paper.path('M 8.7124971,21.247342 L 23.333334,21.247342 L 16.022915,8.5759512 L 8.7124971,21.247342 z');
    path.attr({
      'stroke': strokeColor,
      'stroke-width': 1,
      'fill': fill
    });
    path.transform('T' + (element.x - 1) + ',' + (element.y - 1));
    return path;
  }

  private _drawMessageIcon(paper, element) {
    const strokeColor = this._bpmnGetColor(element, this.MAIN_STROKE_COLOR);

    const path = paper.path('M8,11 L8,21 L24,21 L24,11z M8,11 L16,17 L24,11');
    path.attr({
      'stroke': strokeColor,
      'stroke-width': 1,
      'fill': 'none'
    });
    let xAdder = 0, yAdder = 0;
    if (element.height === 30) {
      xAdder = yAdder = -1;
    } else if (element.height === 36) {
      xAdder = 2;
      yAdder = 1;
    }

    path.transform('T' + (element.x + xAdder) + ',' + (element.y + yAdder));
    return path;
  }

