import { trigger, state, style, animate, query, transition, keyframes } from '@angular/animations';

export const fadeInAnimation = trigger(
    'fadeIn',
    [
      state('false', style({ opacity: 0, display: 'none' })),
      state('true', style({ opacity: 1, display: 'flex' })),
      transition(
        'false => true',
        [
          style(  { opacity: 0, display: 'flex' }),
          query('.json-content', style({ opacity: 0, display: 'flex' })),
          animate('60ms 60ms ease-in', keyframes([
            style({opacity: 0.2, display: 'flex'}),
            style({opacity: 0.5, display: 'flex'}),
            style({opacity: 1,  display: 'flex'}),
          ])),
          query('.json-content', animate('150ms ease-in', keyframes([
            style({opacity: 1,  transform: 'scale(0.5)', display: 'flex'}),
            style({opacity: 1,  transform: 'scale(0.8)', display: 'flex'}),
            style({opacity: 1,  transform: 'scale(1)',   display: 'flex'}),
          ])))
        ]
      ),
      transition(
        'true => false',
        [
          style({  opacity: 1, display: 'flex'}),
          query('.json-content', style({ opacity: 1, display: 'flex' })),
          query('.json-content', animate('200ms ease-out', keyframes([
            style({ transform: 'scale(1)', display: 'flex'}),
            style({ transform: 'scale(0)', display: 'flex'})
          ]))),
          animate('60ms ease-in', keyframes([
            style({opacity: 1, display: 'flex'}),
            style({opacity: 0.5, display: 'flex'}),
            style({opacity: 0.2,  display: 'flex'}),
          ])),
        ]
      )
    ]
);
