import { Component, OnInit } from '@angular/core';
import { SnotifyService } from 'ng-snotify';
import { NotificationService } from '../../services/notification.service';
import { INotification } from '../../model/shared';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.less', './notification.scss']
})
export class NotificationComponent implements OnInit {

    constructor(private snotifyService: SnotifyService, private notifService: NotificationService) { }

    ngOnInit() {
        this.notifService.notificationAction$
            .subscribe((options: INotification) => {
                this.snotifyService[options.type](options.text, '', {
                    timeout: options.timeout || 3000000,
                    position: options.position || 'rightTop',
                    showProgressBar: false
                });
            });
    }
}
