/**
 * Class to generate polyline
 *
 * @author Dmitry Farafonov
 */

const ANCHOR_TYPE = {
  main: 'main',
  middle: 'middle',
  first: 'first',
  last: 'last'
};

class Anchor {
  uuid = null;
  x = 0;
  y = 0;
  type = ANCHOR_TYPE.main;
  isFirst = false;
  isLast = false;
  index = 0;
  typeIndex = 0;

  constructor(uuid, type, x, y, index, typeIndex) {
    this.uuid = uuid;
    this.x = x;
    this.y = y;
    this.type = (type == ANCHOR_TYPE.middle) ? ANCHOR_TYPE.middle : ANCHOR_TYPE.main;
    this.index = index;
    this.typeIndex = typeIndex;
  }
}

export class Polyline {
  /* Array on coordinates:
     * points: [{x: 410, y: 110}, 1
     *			{x: 570, y: 110}, 1 2
     *			{x: 620, y: 240},   2 3
     *			{x: 750, y: 270},     3 4
     *			{x: 650, y: 370}];      4
     */
  id = null;
  points = [];
  path = [];
  anchors = [];
  strokeWidth = 1;
  radius = 1;
  showDetails = false;
  paper = null;
  element = null;
  isDefaultConditionAvailable = false;
  closePath = false;
  
  constructor(uuid, points, strokeWidth, paper) {
    /*
       * path for graph
       * [['M', x1, y1], ['L', x2, y2], ['C', ax, ay, bx, by, x3, y3], ['L', x3, y3]]
       */
    this.points = points;
    this.path = [];
    this.anchors = [];
    if (strokeWidth) this.strokeWidth = strokeWidth;
    this.paper = paper;
    this.closePath = false;

    this.init(this.points);
  }

  init(points) {
    const linesCount = this.getLinesCount();
    if (linesCount < 1)
      return;

    this.normalizeCoordinates();

    // create anchors

    this.pushAnchor(ANCHOR_TYPE.first, this.getLine(0).x1, this.getLine(0).y1);

    for (let i = 1; i < linesCount; i++) {
      const line1 = this.getLine(i - 1);
      this.pushAnchor(ANCHOR_TYPE.main,  line1.x2, line1.y2);
    }

    this.pushAnchor(ANCHOR_TYPE.last, this.getLine(linesCount - 1).x2, this.getLine(linesCount - 1).y2);

    this.rebuildPath();
  }

  normalizeCoordinates() {
    for (let i = 0; i < this.points.length; i++) {
      this.points[i].x = parseFloat(this.points[i].x);
      this.points[i].y = parseFloat(this.points[i].y);
    }
  }

  getLinesCount() {
    return this.points.length - 1;
  }
  
  _getLine(i): any {
    return {x1: this.points[i].x, y1: this.points[i].y, x2: this.points[i + 1].x, y2: this.points[i + 1].y};
  }
  
  getLine(i) {
    const line = this._getLine(i);
    line.angle = this.getLineAngle(i) ;
    return line;
  }
  
  getLineAngle(i) {
    const line = this._getLine(i);
    return Math.atan2(line.y2 - line.y1, line.x2 - line.x1);
  }
  
  getLineLengthX(i) {
    const line = this.getLine(i);
    return (line.x2 - line.x1);
  }
  
  getLineLengthY(i) {
    const line = this.getLine(i);
    return (line.y2 - line.y1);
  }
  getLineLength(i) {
    return Math.sqrt(Math.pow(this.getLineLengthX(i), 2) + Math.pow(this.getLineLengthY(i), 2));
  }

  getAnchors() {
    return this.anchors;
  }
  
  getAnchorsCount(type?): number {
    if (!type)
      return this.anchors.length;
    else {
      let count = 0;
      for (let i = 0; i < this.getAnchorsCount(); i++) {
        const anchor = this.anchors[i];
        if (anchor.getType() == type) {
          count++;
        }
      }
      return count;
    }
  }

  pushAnchor(type, x, y, index?) {
    let typeIndex;
    if (type == ANCHOR_TYPE.first) {
      index = 0;
      typeIndex = 0;
    } else if (type == ANCHOR_TYPE.last) {
      index = this.getAnchorsCount();
      typeIndex = 0;
    } else if (!index) {
      index = this.anchors.length;
    } else {
      for (let i = 0; i < this.getAnchorsCount(); i++) {
        if (this.anchors[i].index > index) {
          this.anchors[i].index++;
          this.anchors[i].typeIndex++;
        }
      }
    }
    let anchor = new Anchor(this.id, ANCHOR_TYPE.main, x, y, index, typeIndex);
    this.anchors.push(anchor);
  }

  getAnchor(position) {
    return this.anchors[position];
  }

  getAnchorByType(type, position) {
    if (type == ANCHOR_TYPE.first)
      return this.anchors[0];
    if (type == ANCHOR_TYPE.last)
      return this.anchors[this.getAnchorsCount() - 1];

    for (let i = 0; i < this.getAnchorsCount(); i++) {
      const anchor = this.anchors[i];
      if (anchor.type == type) {
        if ( position == anchor.position)
          return anchor;
      }
    }
    return null;
  }

  addNewPoint(position, x, y) {
    for (let i = 0; i < this.getLinesCount(); i++) {
      const line = this.getLine(i);
      if (x > line.x1 && x < line.x2 && y > line.y1 && y < line.y2) {
        this.points.splice(i + 1, 0, {x: x, y: y});
        break;
      }
    }
    this.rebuildPath();
  }

  rebuildPath() {
    const path = [];
    let dx0 = null;
    let dy0 = null;
    let ax = null;
    let ay = null;
    let dx1 = null;
    let dy1 = null;
    let bx = null;
    let by = null;
    let zx = null;
    let zy = null;

    for (let i = 0; i < this.getAnchorsCount(); i++) {
      const anchor = this.getAnchor(i);

      let pathType = '';
      if (i == 0)
        pathType = 'M';
      else
        pathType = 'L';

      // TODO: save previous points and calculate new path just if points are updated, and then save currents values as previous

      let targetX = anchor.x, targetY = anchor.y;
      if (i > 0 && i < this.getAnchorsCount() - 1) {
        // get new x,y
        const cx = anchor.x, cy = anchor.y;

        // pivot point of prev line
        let AO1 = this.getLineLength(i - 1);
        if (AO1 < this.radius) {
          AO1 = this.radius;
        }

        this.isDefaultConditionAvailable = (this.isDefaultConditionAvailable || (i == 1 && AO1 > 10));

        let ED1 = this.getLineLengthY(i - 1) * this.radius / AO1;
        let OD1 = this.getLineLengthX(i - 1) * this.radius / AO1;
        targetX = anchor.x - OD1;
        targetY = anchor.y - ED1;

        if (AO1 < 2 * this.radius && i > 1) {
          targetX = anchor.x - this.getLineLengthX(i - 1) / 2;
          targetY = anchor.y - this.getLineLengthY(i - 1) / 2;
        }

        // pivot point of next line
        let AO2 = this.getLineLength(i);
        if (AO2 < this.radius) {
          AO2 = this.radius;
        }
        let ED2 = this.getLineLengthY(i) * this.radius / AO2;
        let OD2 = this.getLineLengthX(i) * this.radius / AO2;
        let nextSrcX = anchor.x + OD2;
        let nextSrcY = anchor.y + ED2;

        if (AO2 < 2 * this.radius && i < this.getAnchorsCount() - 2) {
          nextSrcX = anchor.x + this.getLineLengthX(i) / 2;
          nextSrcY = anchor.y + this.getLineLengthY(i) / 2;
        }

        dx0 = (cx - targetX) / 3;
        dy0 = (cy - targetY) / 3;
        ax = cx - dx0;
        ay = cy - dy0;
        dx1 = (cx - nextSrcX) / 3;
        dy1 = (cy - nextSrcY) / 3;
        bx = cx - dx1;
        by = cy - dy1;
        zx = nextSrcX;
        zy = nextSrcY;
      } else if (i == 1 && this.getAnchorsCount() == 2) {
        let AO3 = this.getLineLength(i - 1);
        if (AO3 < this.radius) {
          AO3 = this.radius;
        }
        this.isDefaultConditionAvailable = (this.isDefaultConditionAvailable || (i == 1 && AO3 > 10));
      }

      // anti smoothing
      if (this.strokeWidth % 2 == 1) {
        targetX += 0.5;
        targetY += 0.5;
      }

      path.push([pathType, targetX, targetY]);

      if (i > 0 && i < this.getAnchorsCount() - 1) {
        path.push(['C', ax, ay, bx, by, zx, zy]);
      }
    }

    if (this.closePath) {
      path.push(['Z']);
    }

    this.path = path;
  }

  transform(transformation) {
    this.element.transform(transformation);
  }

  attr(attrs) {
    // TODO: foreach and set each
    this.element.attr(attrs);
  }
}

