import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatMenuModule} from '@angular/material/menu';
import {MatButtonModule} from '@angular/material/button';
import {MatIconModule} from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatSortModule } from '@angular/material/sort';
import { MatTableModule } from '@angular/material/table';
import {HttpClientModule} from '@angular/common/http';
import {ReactiveFormsModule} from '@angular/forms';
import {MatSelectModule} from '@angular/material/select';
import { HeaderComponent } from '../shared/components/header/header.component';
import { MessagesComponent } from './messages/messages.component';
import { LoginComponent } from './login/login/login.component';
import {MatCardModule} from '@angular/material/card';
import { JsonViewerComponent } from './messages/components/json-popup/json-viewer/json-viewer.component';
import { JsonPopupComponent } from './messages/components/json-popup/json-popup.component';
import { DiagramPopupComponent } from './messages/components/diagram-popup/diagram-popup.component';
import { DiagramViewerComponent } from './messages/components/diagram-popup/diagram-viewer/diagram-viewer.component';
import { SortPipe } from '../shared/pipes/sort.pipe';
import { TruncatePipe } from '../shared/pipes/truncate.pipe';
import { MessagesService } from '../shared/services/messages.service';
import { DragScrollModule } from '../shared/components/drag-scroll';
import {SnotifyModule, SnotifyService, ToastDefaults} from 'ng-snotify';
import { NotificationService } from '../shared/services/notification.service';
import { NotificationComponent } from '../shared/components/notification/notification.component';
@NgModule({
    declarations: [
        AppComponent,
        MessagesComponent,
        HeaderComponent,
        LoginComponent,
        JsonPopupComponent,
        JsonViewerComponent,
        SortPipe,
        TruncatePipe,
        DiagramPopupComponent,
        DiagramViewerComponent,
        NotificationComponent
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        HttpClientModule,
        MatMenuModule,
        MatButtonModule,
        MatIconModule,
        MatInputModule,
        MatCardModule,
        MatTableModule,
        MatSelectModule,
        MatPaginatorModule,
        MatSortModule,
        MatProgressSpinnerModule,
        AppRoutingModule,
        ReactiveFormsModule,
        DragScrollModule,
        SnotifyModule
    ],
    providers: [
        MessagesService,
        SnotifyService,
        NotificationService,
        { provide: 'SnotifyToastConfig', useValue: ToastDefaults},
    ],
    bootstrap: [AppComponent],
    entryComponents: [MessagesComponent]
})
export class AppModule {
}
