import {AfterViewInit, Component, ElementRef, OnInit, ViewChild, EventEmitter} from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { tap, debounceTime, distinctUntilChanged, delayWhen } from 'rxjs/operators';
import {merge, fromEvent, interval, of} from 'rxjs';

import { DATE_FILTER_OPTIONS } from './messages.constants';
import { DatePeriod, IPopupParams } from '../../shared/model/shared';
import { CustomDataSource } from '../../shared/utils/custom.datasource';
import { MessagesService } from '../../shared/services/messages.service';
import { NotificationService } from '../../shared/services/notification.service';

@Component({
    selector: 'app-messages',
    templateUrl: './messages.component.html',
    styleUrls: ['./messages.component.less']
})
export class MessagesComponent implements OnInit, AfterViewInit {

    public dateFilterOptions: Array<DatePeriod> = DATE_FILTER_OPTIONS;
    public dateFilterValue = '0';

    public dataSource: CustomDataSource;
    public displayedColumns: Array<string> = ['id', 'receiveDate', 'state', 'channel', 'route', 'destination'];

    public isLoading = false;

    public jsonPopupEmitter:    EventEmitter<IPopupParams> = new EventEmitter();
    public diagramPopupEmitter: EventEmitter<IPopupParams> = new EventEmitter();

    @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
    @ViewChild(MatSort, { static: true })      sort: MatSort;

    @ViewChild('inputSearch', { static: true })    inputSearch: ElementRef;
    @ViewChild('inputPaginator', { static: true }) inputPaginator: ElementRef;



    constructor(private messagesService: MessagesService, private notificationService: NotificationService) { }


    ngOnInit() {
        this.dataSource = new CustomDataSource(this.messagesService);
        this.dataSource.loadMessages({sortField: 'receiveDate', page: 1, rowCount: 7, minDate: this.getMinDate(0)});
        this.dataSource.loading$
            .pipe( delayWhen(isLoading => isLoading ? of(0) : interval(700)) )
            .subscribe(res => this.isLoading = res);
    }

    private loadMessagesPage() {
        this.dataSource.loadMessages({
            sortField: this.sort.active,
            sortDirection: this.sort.direction === 'asc' ? 0 : 1,
            page: this.paginator.pageIndex + 1,
            rowCount: this.paginator.pageSize,
            minDate: this.getMinDate(this.dateFilterValue),
            filterValue: this.inputSearch.nativeElement.value
        });
    }

    private getMinDate(daysToSubtract) {
        if (!daysToSubtract && daysToSubtract !== 0) { return ''; }

        const dateObject = new Date();
        const alteredDate = dateObject.getDate() - daysToSubtract;
        dateObject.setDate(alteredDate);
        return this.getParsedDate(dateObject);
    }

    private getParsedDate(date = new Date()) {
        const year = date.getFullYear();
        let month: number | string = date.getMonth() + 1;
        if (month < 10) {
          month = `0${month}`;
        }
        let day: number | string = date.getDate();
        if (day < 10) {
          day = `0${day}`;
        }
        return `${day}.${month}.${year}`;
    }

    public datePeriodChange() {
        this.loadMessagesPage();
    }

    private openJsonPopup(index) {
        this.jsonPopupEmitter.emit({ state: true, data: this.messagesService.rawMessagedata[index] });
    }

    private openDiagram(id) {
        this.isLoading = true;
        this.messagesService.getDiagram(id)
            .subscribe(res => {
                this.diagramPopupEmitter.emit( {state: true, data: res});
                this.isLoading = false;
            });
    }

    public performAction(action, message, index) {
        switch (action) {
            case 'view':
                this.openJsonPopup(index);
                break;
            case 'openDiagram':
                this.openDiagram(message.leadId);
                break;
            case 'resend':
                this.messagesService.resendMessage(message.id)
                    .subscribe(res => this.notificationService.showNotification({text: '  ', type: 'success'}));
                break;
             case 'execute':
                this.messagesService.executeMessage(message.id)
                    .subscribe(res => this.notificationService.showNotification({text: '  ', type: 'success'}));
                break;
            case 'fixAndExecute':
                this.messagesService.migrateMessage(message.id)
                    .subscribe(res => this.notificationService.showNotification({text: '  ', type: 'success'}));
                break;
        }
    }

    ngAfterViewInit() {

        this.setOnFilterChange();

        this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);

        merge(this.sort.sortChange, this.paginator.page)
            .pipe(tap(() => this.loadMessagesPage()))
            .subscribe();

    }

    private setOnFilterChange() {
        fromEvent(this.inputSearch.nativeElement, 'keyup')
        .pipe(
            debounceTime(150),
            distinctUntilChanged(),
            tap(() => {
                this.paginator.pageIndex = 0;

                this.loadMessagesPage();
            })
        )
        .subscribe();

        fromEvent(this.inputPaginator.nativeElement, 'keyup')
        .pipe(
            debounceTime(300),
            distinctUntilChanged(),
            tap((res) => {
                this.paginator.pageIndex = this.inputPaginator.nativeElement.value;

                this.loadMessagesPage();
            })
        )
        .subscribe();
    }

}
