

import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Subject, pipe, of} from 'rxjs';
import {Message} from '../model/messages';
import {map, tap, catchError} from 'rxjs/operators';
import { MENU_ACTIONS } from '../../app/messages/messages.constants';
import { NotificationService } from './notification.service';


@Injectable()
export class MessagesService {

    private totalRowsSubject: Subject<any> = new Subject();
    public totalRows = this.totalRowsSubject.asObservable();

    private _rawMessageData;

    public get rawMessagedata() {
        return this._rawMessageData;
    }

    constructor(private http: HttpClient, private notificationService: NotificationService) {

    }

    public resendMessage(id) {
        return this.http.post(`http://192.168.7.24:5555/enoti/v1/message/resend?msgId=${id}`, {})
            .pipe(catchError(() =>  of(this.notificationService.showNotification({text: '   ', type: 'error', position: 'rightTop'}))));
    }

    public migrateMessage(id) {
        return this.http.post(`http://192.168.7.24:5555/enoti/v1/message/job/migration?msgId=${id}`, {})
            .pipe(catchError(() =>  of(this.notificationService.showNotification({text: '    ', type: 'error', position: 'rightTop'}))));
    }

    public executeMessage(id) {
        return this.http.post(`http://192.168.7.24:5555/enoti/v1/message/job/execute?msgId=${id}`, {})
            .pipe(catchError(() =>  of(this.notificationService.showNotification({text: '  ', type: 'error', position: 'rightTop'}))));
    }

    public getDiagram(id) {
        return this.http.get(`http://192.168.7.24:5555/enoti/diagram/v1/instance?id=${id}`, {})
            .pipe(catchError(() =>  of(this.notificationService.showNotification({text: '   ', type: 'error', position: 'rightTop'}))));
    }

    getData(body) {
        return this.http.post('http://192.168.7.24:5555/enoti/v1/message', body)
            .pipe(
                tap((res: any) => this.messagesDataSideEffect(res)),
                map(this.handleData),
                catchError(() =>  of(this.notificationService.showNotification({text: '   ', type: 'error', position: 'rightTop'})))
            );
    }

    private handleData(response): Array<Message> {
        return response.data.map(item => {
            const message = Object.assign({}, item.header, item.description );
            message.actions = [MENU_ACTIONS.view, MENU_ACTIONS.viewDiagram];
            const badStates = ['PARTIALLY', 'UNDELIV', 'EXPIRED'];
            if (message.error && message.error.code === 'JOB') {
                message.actions = message.actions.concat([MENU_ACTIONS.execute, MENU_ACTIONS.fixAndExecute]);
            }
            if (badStates.includes(message.status) && message.textSave) {
                message.actions.push(MENU_ACTIONS.resend);
            }
            if ((message.status === 'ACCEPTED' || message.status === 'RECEIVED') && message.error !== 'JOB' && message.textSave) {
                message.actions.push(MENU_ACTIONS.resend);
            }
            if (message.error) {
                console.log(message.error);
                console.log(message.id);
            }
            return message;
        });
    }

    private messagesDataSideEffect(res) {
        this.totalRowsSubject.next(res.totalRows);
        this._rawMessageData = res.data;
     }

}
