import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'truncatePipe'
})
export class TruncatePipe implements PipeTransform {

  transform(value: string, args: {length?: number, ending?: string} = {} ): string {
    if (!value) {
      return '-';
    } else if (!args.length) {
      return value;
    } else {
      return this.textTruncate(value, args.length, args.ending);
    }
  }

  private textTruncate(str, length = 100, ending = '...') {
    if (str && str.length && str.length > length) {
      return str.substring(0, length - ending.length) + ending;
    } else if (str && str.length) {
      return str;
    } else {
      return '-';
    }
  }

}
