import { Component, OnChanges, Input } from '@angular/core';

export interface Segment {
  key: string;
  value: any;
  type: undefined | string;
  description: string;
  expanded: boolean;
  edit: boolean;
}

export const ParseKeyValue = (key: any, value: any, state: boolean): Segment => {
  const segment: Segment = {
    key: key,
    value: value,
    type: undefined,
    description: '' + value,
    expanded: state,
    edit: false,
  };

  const truncateString = (str, strEnd: string = '') => {
    return str.length > 80 ? str.slice(0, 80) + '...' + strEnd : str;
  };

  switch (typeof segment.value) {
    case 'number': {
      segment.type = 'number';
      segment.edit = true;
      break;
    }
    case 'boolean': {
      segment.type = 'boolean';
      segment.edit = true;
      break;
    }
    case 'function': {
      segment.type = 'function';
      break;
    }
    case 'string': {
      segment.type = 'string';
      segment.edit = true;
      segment.description = '"' + segment.value.replace(/(\r\n|\n|\r)/gm, '<br>') + '"';
      break;
    }
    case 'undefined': {
      segment.type = 'undefined';
      segment.description = 'undefined';
      break;
    }
    case 'object': {
      // yea, null is object
      if (segment.value === null) {
        segment.type = 'null';
        segment.description = 'null';
      } else if (Array.isArray(segment.value)) {
        segment.type = 'array';
        const str = JSON.stringify(segment.value);
        const description = truncateString(str, '"}]');
        segment.description = 'Array[' + segment.value.length + '] ' + description;
      } else if (segment.value instanceof Date) {
        segment.type = 'date';
      } else {
        segment.type = 'object';
        const str = JSON.stringify(segment.value);
        const description = truncateString(str, '"}');
        segment.description = 'Object ' + description;
      }
      break;
    }
  }

  return segment;
};

@Component({
  selector: 'app-json-viewer',
  templateUrl: './json-viewer.component.html',
  styleUrls: ['./json-viewer.scss', './json-viewer.component.less']
})
export class JsonViewerComponent implements OnChanges {

  @Input() data: any;
  @Input() parent: string;
  @Input() expanded = false;
  @Input() state = false;
  @Input() truncate = false;
  @Input() truncateStrLength = 110;


  segments: Segment[] = [];

  constructor() { }

  ngOnChanges() {
    this.segments = [];
    if (typeof this.data === 'object') {
      Object.keys(this.data).forEach( key => {
        this.segments.push(ParseKeyValue(key, this.data[key], this.expanded));
      });
    } else {
      this.segments.push(ParseKeyValue('Message', this.data, this.expanded));
    }
  }

  get truncateLength() {
    return this.truncate ? this.truncateStrLength : null;
  }


  isExpandable(segment: Segment) {
    return segment.type === 'object' || segment.type === 'array';
  }

  toggle(segment: Segment) {
    if (this.isExpandable(segment)) {
      segment.expanded = !segment.expanded;
    }
  }


}
