import { Component, Input, HostListener, OnInit, EventEmitter } from '@angular/core';
import { fadeInAnimation } from '../../../../shared/utils/animations';

@Component({
  selector: 'app-json-popup',
  templateUrl: './json-popup.component.html',
  styleUrls: ['./json-popup.component.less'],
  animations: [ fadeInAnimation ]
})
export class JsonPopupComponent implements OnInit {

  @Input() truncate: boolean;
  @Input() popupAction: EventEmitter<any> = new EventEmitter();

  public state = false;
  public data;

  constructor() { }

  ngOnInit() {
    this.popupAction
      .subscribe(value => {
        console.log(value.data)
        this.state = value.state;
        this.data = value.data;
      });
  }


  public closeJsonTab() {
    this.state = false;
  }

  @HostListener('window:keyup', ['$event'])
  keyEvent(event: KeyboardEvent) {
      if (event.keyCode === 27) { // 27 - Esc keycode
      this.state = false;
      }
  }

}
