
interface ISortable {
  dataList: Array<any>;
  sortParam: string;
}

export class SortUtils {
  public static customSort<T>(data: ISortable): Array<T> {
    return data.dataList.sort((a, b) => {
      if (!isNaN(a[data.sortParam]) && !isNaN(b[data.sortParam])) {
        if (Number(a[data.sortParam]) < Number(b[data.sortParam])) { return -1; }
        if (Number(a[data.sortParam]) > Number(b[data.sortParam])) { return 1; }
      } else if (typeof a[data.sortParam] === 'boolean' || typeof b[data.sortParam] === 'boolean') {
        const x = isNaN(+a[data.sortParam]) ? 0 : +a[data.sortParam];
        const y = isNaN(+b[data.sortParam]) ? 0 : +b[data.sortParam];
        if (x < y) { return -1; }
        if (x > y) { return 1; }
      } else if (a[data.sortParam] && b[data.sortParam]) {
        if (a[data.sortParam].toString().toLowerCase() < b[data.sortParam].toString().toLowerCase()) { return -1; }
        if (a[data.sortParam].toString().toLowerCase() > b[data.sortParam].toString().toLowerCase()) { return 1; }
      } else if (
        (a[data.sortParam] === undefined || a[data.sortParam] === null) &&
        (b[data.sortParam] === undefined || b[data.sortParam] === null)) {
        return 0;
      } else if (a[data.sortParam] === undefined || a[data.sortParam] === null) {
        return 1;
      } else if (b[data.sortParam] === undefined || b[data.sortParam] === null) {
        return -1;
      } else {
        return 0;
      }
    });
  }

  public static sortObject(o) {
    return Object.keys(o).sort().reduce((r, k) => (r[k] = o[k], r), {});
  }

}
