


import {CollectionViewer, DataSource} from '@angular/cdk/collections';
import {Observable, BehaviorSubject, of} from 'rxjs';
import {catchError, finalize} from 'rxjs/operators';
import { MessagesService } from '../services/messages.service';
import { Message } from '../model/messages';
import { IMessagesRequestBody } from '../model/shared';



export class CustomDataSource implements DataSource<Message> {

    private messagesAsyncData: BehaviorSubject<Message[]>  = new BehaviorSubject<Message[]>([]);

    private loadingSubject = new BehaviorSubject<boolean>(false);
    public loading$ = this.loadingSubject.asObservable();

    constructor(private messagesService: MessagesService) { }

    public loadMessages(body: IMessagesRequestBody) {
        this.loadingSubject.next(true);

        this.messagesService.getData(body).pipe(
                catchError(() => of([])),
                finalize(() => this.loadingSubject.next(false))
            )
            .subscribe((messages: Message[]) => { console.log(messages); return this.messagesAsyncData.next(messages); });

    }

    connect(collectionViewer: CollectionViewer): Observable<Message[]> {
        return this.messagesAsyncData.asObservable();
    }

    disconnect(collectionViewer: CollectionViewer): void {
        this.messagesAsyncData.complete();
        this.loadingSubject.complete();
    }

}

