

import {Injectable} from '@angular/core';
import { Subject } from 'rxjs';
import { INotification } from '../model/shared';


@Injectable()
export class NotificationService {
    private notificationAction: Subject<INotification> = new Subject();
    public notificationAction$ = this.notificationAction.asObservable();

    constructor() {}

    public showNotification(options: INotification) {
        this.notificationAction.next(options);
    }
}
